// @mui material components
import Container from "@mui/material/Container";
import Grid from "@mui/material/Grid";
import Autocomplete from "@mui/material/Autocomplete";

// Otis Kit PRO components
import MKBox from "components/MKBox";
import MKInput from "components/MKInput";
import { useState } from "react";
import { PropTypes } from "prop-types";
import useStore from "promessa/store";

function Buscador({ ciudad, estado, sucursal, setAsesores }) {
  const [inputValue, setInputValue] = useState("");
  const [options, setOptions] = useState([]);
  //const [value, setValue] = useState(null);

  const { cargarAsesoresEnSucursalPorNombre } = useStore();

  const handleKeyDown = async (event) => {
    if (event.key === "Enter") {
      event.preventDefault(); // Previene el comportamiento por defecto de Enter
      if (inputValue.length < 2) {
        alert("Inserta al menos 2 caractres para la bsqueda-");
        return;
      }

      const newOptions = await cargarAsesoresEnSucursalPorNombre(
        ciudad,
        estado,
        sucursal,
        inputValue
      );

      /*const newOptions = resultadosAsesores.filter((option) =>
        option.name.toLowerCase().includes(inputValue.toLowerCase())
      );*/
      setOptions(newOptions);
      setAsesores(newOptions);
    }
  };

  return (
    <MKBox component="section" py={12}>
      <Container>
        <Grid container justifyContent="center">
          <Autocomplete
            options={options}
            sx={{ width: 300 }}
            renderInput={(params) => (
              <MKInput
                {...params}
                label="Buscar Asesores"
                value={inputValue}
                onChange={(e) => setInputValue(e.target.value)}
                onKeyDown={handleKeyDown}
              />
            )}
            getOptionLabel={(opt) => opt.Nombre}
          />
        </Grid>
      </Container>
    </MKBox>
  );
}

Buscador.propTypes = {
  //resultadosAsesores: PropTypes.array.isRequired,
  ciudad: PropTypes.string.isRequired,
  estado: PropTypes.string.isRequired,
  sucursal: PropTypes.string.isRequired,
  setAsesores: PropTypes.func.isRequired,
};

export default Buscador;
